<template>
  <v-container>
    <v-row>
      <!-- Tá»« ngÃ y -->
      <v-col cols="12" sm="6" md="4">
        <v-menu
          ref="fromMenu"
          v-model="fromMenu"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          min-width="auto"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              label="Tá»« ngÃ y"
              v-model="fromDate"
              readonly
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="fromDate"
            @input="fromMenu = false"
          ></v-date-picker>
        </v-menu>
      </v-col>

      <!-- Äáº¿n ngÃ y -->
      <v-col cols="12" sm="6" md="4">
        <v-menu
          ref="toMenu"
          v-model="toMenu"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          min-width="auto"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              label="Äáº¿n ngÃ y"
              v-model="toDate"
              readonly
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="toDate"
            @input="toMenu = false"
          ></v-date-picker>
        </v-menu>
      </v-col>

      <!-- IGATE Token -->
      <v-col cols="12">
        <textarea
          class="textarea"
          v-model="igateToken"
          required
          placeholder="DÃ¡n IGATE Token vÃ o ÄÃ¢y"
          style="
            width: 100%;
            height: 100px;
            box-sizing: border-box;
            border: 1px solid #bbbbbb !important;
          "
        ></textarea>
      </v-col>

      <!-- NÃºt táº£i bÃ¡o cÃ¡o -->
      <v-col cols="12">
        <v-btn color="primary" @click="exportDossier">Táº£i bÃ¡o cÃ¡o</v-btn>
      </v-col>

      <!-- Báº£ng káº¿t quáº£ -->
      <v-col cols="12">
        <v-data-table
          :headers="tableHeaders"
          :items="tableItems"
          class="elevation-1"
          :items-per-page="50"
        >
          <template v-slot:top>
            <v-toolbar flat color="white">
              <v-toolbar-title>Káº¿t quáº£ bÃ¡o cÃ¡o</v-toolbar-title>
            </v-toolbar>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";
import * as XLSX from "xlsx";
import dviJson2 from "./dviJson2.json";

export default {
  data() {
    return {
      igateToken: "",
      json2: dviJson2,
      fromDate: this.formatDate(new Date()),
      toDate: this.formatDate(new Date()),
      fromMenu: false,
      toMenu: false,

      tableHeaders: [
        { text: "STT", value: "stt", width: 50 },
        { text: "Äá»a phÆ°Æ¡ng", value: "name" },
        { text: "Tá»NG HS", value: "tong" },
        { text: "Ná»P TRá»°C TUYáº¾N", value: "online" },
        { text: "Ná»P TRá»°C TIáº¾P", value: "offline" },
        { text: "HoÃ n thÃ nh", value: "resolved" },
        { text: "Äang xá»­ lÃ½", value: "unresolved" },
        { text: "Tá»n ká»³ trÆ°á»c", value: "tonKy" },
      ],
      tableItems: [],
    };
  },
  methods: {
    formatDate(date) {
      const d = new Date(date);
      const year = d.getFullYear();
      const month = ("0" + (d.getMonth() + 1)).slice(-2);
      const day = ("0" + d.getDate()).slice(-2);
      return `${year}-${month}-${day}`;
    },

    async exportDossier() {
      const fromISO = `${this.fromDate}T00:00:00.000Z`;
      const toISO = `${this.toDate}T23:59:59.999Z`;
      const apiUrl = `https://apiigate.gialai.gov.vn/pa/dni-dossier-statistic/--01-2020-percentage?from-date=${fromISO}&to-date=${toISO}&type=&agency-id=60b87fb59adb921904a0213e`;

      try {
        const response = await axios.get(apiUrl, {
          headers: {
            Authorization: `Bearer ${this.igateToken}`,
          },
        });

        const data = response.data || [];
        const dataMap = {};
        for (const item of data) {
          if (item.agency?.code) {
            dataMap[item.agency.code] = item;
          }
        }

        const output = [];
        this.tableItems = [];
        let stt = 1;

        for (const dv of this.json2) {
          const code = dv["MA DON VI"];
          const name = dv["DON VI"];
          const item = dataMap[code];

          const tong = item?.procedureLevelUnknown || 0;
          const online = item?.receivedOnline || 0;
          const offline = item?.receivedDirect || 0;
          const resolved = item?.resolved || 0;
          const unresolved = item?.unresolved || 0;
          const tonKy = item?.receivedOld || 0;

          output.push([
            stt,
            name,
            tong,
            online,
            offline,
            resolved,
            unresolved,
            tonKy,
            name,
          ]);

          this.tableItems.push({
            stt: stt++,
            name,
            tong,
            online,
            offline,
            resolved,
            tonKy,
            unresolved,
          });
        }

        const header = [
          [
            "STT",
            "Äá»a phÆ°Æ¡ng",
            "Tá»NG HS",
            "Trong ÄÃ³",
            "",
            "Trong ÄÃ³",
            "",
            "Tá»n ká»³ trÆ°á»c",
            "Ghi chÃº",
          ],
          [
            "",
            "",
            "",
            "Ná»P\nTRá»°C TUYáº¾N",
            "Ná»P\nTRá»°C TIáº¾P",
            "HoÃ n thÃ nh",
            "Äang xá»­ lÃ½",
            "",
            "",
          ],
        ];

        const sheetData = [...header, ...output];
        const ws = XLSX.utils.aoa_to_sheet(sheetData);

        ws["!merges"] = [
          { s: { r: 0, c: 0 }, e: { r: 1, c: 0 } },
          { s: { r: 0, c: 1 }, e: { r: 1, c: 1 } },
          { s: { r: 0, c: 2 }, e: { r: 1, c: 2 } },
          { s: { r: 0, c: 3 }, e: { r: 0, c: 4 } },
          { s: { r: 0, c: 5 }, e: { r: 0, c: 6 } },
          { s: { r: 0, c: 7 }, e: { r: 1, c: 7 } },
        ];

        const wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, "BÃ¡o cÃ¡o");

        XLSX.writeFile(wb, "bao_cao_giailai.xlsx");
      } catch (err) {
        console.error("Lá»i khi táº£i hoáº·c xá»­ lÃ½ dá»¯ liá»u:", err);
        alert("KhÃ´ng thá» táº¡o bÃ¡o cÃ¡o. Kiá»m tra token hoáº·c káº¿t ná»i.");
      }
    },
  },
};
</script>
