import * as XLSX from "xlsx";

export default {
  data() {
    return {
      templateSheet: [],
      danhsachSheet: [],
      fileReady: false,
    };
  },
  methods: {
    handleFileUpload(fileList) {
      const file = fileList instanceof FileList ? fileList[0] : fileList;

      const reader = new FileReader();
      reader.onload = (e) => {
        const data = new Uint8Array(e.target.result);
        const workbook = XLSX.read(data, { type: "array" });

        // Äá»c sheet Template
        const templateSheet = XLSX.utils.sheet_to_json(
          workbook.Sheets["Template2"],
          { defval: "" }
        );

        this.templateSheet = templateSheet.map((row) => ({
          maTTHC: row["MÃ£ TTHC"],
          tenTTHC: row["TÃªn TTHC"],
          cap: row["CAP"],
          donvi: row["DONVI"],
        }));
        console.log("templateSheet", this.templateSheet);

        // Äá»c sheet danhsach

        const danhsach = XLSX.utils.sheet_to_json(workbook.Sheets["danhsach"], {
          defval: "",
        });
        this.danhsachSheet = danhsach.map((row) => ({
          ma: row["MÃ£ ÄÆ¡n vá»/phÃ²ng, ban"],
          cap: row["Cáº¥p ÄÆ¡n vá»/phÃ²ng, ban"],
        }));
        console.log("danhsachSheet", this.danhsachSheet);
        // Cho phÃ©p báº¥m nÃºt
        this.fileReady = true;
      };

      reader.readAsArrayBuffer(file);
    },

    generateAndExport() {
      const output = [];

      this.templateSheet.forEach((row) => {
        const caps = (row.cap || "").split(";").map((c) => c.trim());

        let isFirst = true;

        // Náº¿u cÃ³ Cáº¥p Tá»nh
        if (caps.includes("Cáº¥p Tá»nh")) {
          output.push({
            "MÃ£ TTHC": isFirst ? row.maTTHC : "",
            "TÃªn TTHC": isFirst ? row.tenTTHC : "",
            "ÄÆ¡n vá»": row.donvi,
            Cáº¥p: "Cáº¥p Tá»nh",
          });
          isFirst = false;
        }

        // Vá»i Cáº¥p Huyá»n & XÃ£
        ["Cáº¥p Huyá»n", "Cáº¥p XÃ£"].forEach((capTarget) => {
          if (caps.includes(capTarget)) {
            this.danhsachSheet
              .filter((d) => d.cap === capTarget)
              .forEach((d) => {
                output.push({
                  "MÃ£ TTHC": isFirst ? row.maTTHC : "",
                  "TÃªn TTHC": isFirst ? row.tenTTHC : "",
                  "ÄÆ¡n vá»": d.ma,
                  Cáº¥p: capTarget,
                });
                isFirst = false;
              });
          }
        });
      });

      // Xuáº¥t file Excel
      const ws = XLSX.utils.json_to_sheet(output);
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, "KetQua");
      XLSX.writeFile(wb, "ket_qua_tthc.xlsx");
    },

