<script>
import axios from "axios";

export default {
  components: {},
  watch: {},
  computed: {
    iconStyle() {
      return {
        backgroundColor: "#1976d2 !important" /* MÃ u ná»n cá»§a icon */,
        color: "white" /* MÃ u cá»§a icon */,
        borderRadius: "50%" /* Bo trÃ²n Äá» táº¡o hÃ¬nh trÃ²n */,
        padding: "10px" /* ThÃªm khÃ´ng gian xung quanh icon */,
        fontSize: "25px" /* KÃ­ch thÆ°á»c icon */,
        marginBottom: "5px",
      };
    },
    closeIconStyle() {
      return {
        position: "absolute" /* Äáº·t vá» trÃ­ tuyá»t Äá»i cho icon ÄÃ³ng */,
        right: "16px" /* CÄn chá»nh tá»« bÃªn pháº£i */,
        top: "16px" /* CÄn chá»nh tá»« trÃªn cÃ¹ng */,
        cursor: "pointer" /* Thay Äá»i con trá» khi hover */,
        color: "white" /* MÃ u cá»§a icon, cÃ³ thá» thay Äá»i theo Ã½ muá»n */,
        borderRadius: "50%",
        backgroundColor: "#1976d2 !important",
        padding: "5px" /* ThÃªm khÃ´ng gian xung quanh icon */,
        fontSize: "15px" /* KÃ­ch thÆ°á»c icon */,
      };
    },

    imageStyle() {
      return {
        width: "100%",
        height: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
      };
    },
    isAllSuccess() {
      return (
        this.ketQuaNhieu.length > 0 &&
        this.ketQuaNhieu.every((item) => item.status === "success")
      );
    },
  },
  data() {
    return {
      igateToken: "",
      maHso: "",
      maHso2: "",
      ketQuaDialog: false,
      isSuccess: false,
      ketQuaNhieu: [],
      ketQua: "",
      loading: false,
      module: "",
      hsoId: "",
      dialog: false,
      dialogLT: false,
      callAgainTextarea: "",
      tableHeaders: [
        { text: "Dossier Code", value: "dossierCode", align: "start" },
        { text: "Call Time", value: "callTime", align: "start" },
        { text: "Request Body", value: "requestBody", align: "start" },
        { text: "Response Body", value: "responseBody", align: "end" },
        // { text: "Security Key", value: "securityKey" },
        { text: "Trang Thai", value: "requestBody.trangThai" },
        { text: "Action", value: "action", sortable: false, align: "end" },
      ],
      data: [],
      trangThaiHoSo: {},
      trangThaiHoSoString: "",
      istrangThaiHoSoSuccess: false,
      trangThaiHienTai: "",
    };
  },
  methods: {
    async fetchData() {
      this.loading = true;
      if (this.maHso2 != "") {
        await this.getHsoId();
      }
      this.getTTHosO();
    },
    clearText() {
      this.maHso2 = ""; // XÃ³a ná»i dung input
    },
    clearText2() {
      this.maHso = ""; // XÃ³a ná»i dung input
    },
    showError() {
      this.ketQuaDialog = true; // Má» dialog
      this.loading = false;
    },

    extractMaHo() {
      const match = this.maHso.match(/(H21|G22)\.\d{2}\.\d{2}-\d{6}-\d{4}/);
      const match2 = this.maHso2.match(/(H21|G22)\.\d{2}\.\d{2}-\d{6}-\d{4}/);
      if (match) {
        this.maHso = match[0]; // GÃ¡n láº¡i chá» pháº§n cáº§n láº¥y
      } else if (match2) {
        this.maHso2 = match2[0];
      } else {
        alert("KhÃ´ng tÃ¬m tháº¥y mÃ£ há» sÆ¡ há»£p lá»!");
      }
    },

    async endHoSo() {
      this.loading = true;
      await this.getHsoId();
      if (this.hsoId) {
        await this.endProcess();
      }
    },

    async endProcess() {
      if (this.hsoId == "") {
        alert("KhÃ´ng cÃ³ thÃ´ng tin");
      } else {
        const url =
          "https://apiigate.gialai.gov.vn/pa/dossier/--force-end-process";
        var endRequestBody = [
          {
            id: this.hsoId,
            code: this.maHso2,
          },
        ]; // Chuyá»n requestBodyString vá» dáº¡ng object
        console.log(this.requestBody);
        try {
          const response = await axios.put(url, endRequestBody, {
            headers: {
              Authorization: `Bearer ${this.igateToken}`,
              "Content-Type": "application/json", // Content-Type cá»§a body lÃ  JSON
            },
          });
          console.log("Response:", response.data);

          alert("Káº¿t thÃºc thÃ nh cÃ´ng");
        } catch (error) {
          console.error(
            "Error:",
            error.response ? error.response.data : error.message
          );
        } finally {
          alert("KhÃ´ng cÃ³ thÃ´ng tin");
        }
      }
    },

    copyToClipboard(item) {
      this.dialog = true;
      console.log(item);
      this.callAgainTextarea = JSON.stringify(item, null, 2);
    },

    async checkTrangThai() {
      const response = await axios.post(
        "https://apiigate.gialai.gov.vn/ad/service/judicial-civil-status/--dossiers",
        {
          maTinh: "64",
          maDinhDanhHoSo: [this.maHso2],
          module: this.module, // Ná»i tiá»n tá» vá»i name
        },
        {
          headers: {
            Authorization: `Bearer ${this.igateToken}`, // ÄÃ­nh kÃ¨m token vÃ o header
          },
        }
      );
      if (response) {
        this.istrangThaiHoSoSuccess = true;
        this.trangThaiHoSo = response.data.value[0];
        this.trangThaiHoSoString = JSON.stringify(this.trangThaiHoSo, null, 2);
        this.trangThaiHienTai = response.data.value[0].trangThai;
      }
    },

    async updateTrangThai() {},

    async getHsoId() {
      const maHsoTrimmed = this.maHso2.trim().replace(/\s+/g, "");
      const getHsoIdUrl = `https://apiigate.gialai.gov.vn/pa/dossier/search?page=0&size=20&applicant-organization=&spec=slice&code=${maHsoTrimmed}`;

      try {
        const getHsId = await axios.get(getHsoIdUrl, {
          headers: {
            Authorization: `Bearer ${this.igateToken}`,
          },
        });
        if (getHsId.data.content[0]) {
          this.maHso2 = getHsId.data.content[0].code;
          this.maHso = getHsId.data.content[0].nationCode;
          this.module = getHsId.data.content[0].eForm.data.loaiHTTP;
          this.hsoId = getHsId.data.content[0].id;
        }
      } catch (error) {
        alert("lá»I");
        if (error.response) {
          console.error("Dá»¯ liá»u pháº£n há»i lá»i:", error.response.data);
          console.error("Tráº¡ng thÃ¡i pháº£n há»i lá»i:", error.response.status);
        } else if (error.request) {
          console.error("YÃªu cáº§u lá»i:", error.request);
        } else {
          console.error("ThÃ´ng bÃ¡o lá»i:", error.message);
        }
      }
    },

    async keoHoSo() {
      this.loading = true;
      const nationCode = this.maHso.trim();
      if (!nationCode) {
        alert("Vui lÃ²ng nháº­p mÃ£ há» sÆ¡!");
        return;
      }
      if (!this.igateToken) {
        alert("Vui lÃ²ng nháº­p token!");
        return;
      }

      try {
        const resendResponse = await axios.post(
          // "https://wsms.gialai.vnpt.vn/wsms/api/igate/resend-one",
          "https://wsms.gialai.vnpt.vn/wsms/api/igate/resend-one",
          {
            nationCode,
            token: this.igateToken,
          }
        );
        console.log(resendResponse);

        const result = resendResponse.data;
        if (result.status === "success") {
          // alert("Gá»­i láº¡i thÃ nh cÃ´ng!");
          this.ketQua = "Gá»­i láº¡i thÃ nh cÃ´ng!";
          this.isSuccess = true;
          this.showError();
        } else {
          this.ketQua = "Gá»­i láº¡i tháº¥t báº¡i: " + JSON.stringify(result);
          this.isSuccess = false;
          this.showError();
        }
      } catch (error) {
        console.error("Lá»i:", error);
        this.isSuccess = false;
        this.ketQua =
          "Gá»­i láº¡i tháº¥t báº¡i: " + (error.response?.data || error.message);
        this.showError();
      }
    },

    async resendNhieuHoSo() {
      this.loading = true;
      const rawList = this.maHso
        .split(",")
        .map((s) => s.trim())
        .filter((s) => s.length > 0);

      if (!this.igateToken || rawList.length === 0) {
        alert("Vui lÃ²ng nháº­p mÃ£ há» sÆ¡ vÃ  token.");
        return;
      }

      this.ketQuaNhieu = [];

      for (let ma of rawList) {
        try {
          const res = await axios.post(
            "https://wsms.gialai.vnpt.vn/wsms/api/igate/resend-one",
            {
              nationCode: ma,
              token: this.igateToken,
            }
          );

          this.ketQuaNhieu.push({
            nationCode: ma,
            status: res.data.status,
            message: res.data.message || "",
          });
        } catch (err) {
          this.ketQuaNhieu.push({
            nationCode: ma,
            status: "error",
            message: err.message || "Lá»i khÃ´ng xÃ¡c Äá»nh",
          });
        }
      }
      this.loading = false;
      this.ketQuaDialog = true;
    },

    async getTTHosO() {
      const maHsoTrimmed = this.maHso.trim().replace(/\s+/g, "");
      this.data = [];

      const getHsoIdUrl = `https://apiigate.gialai.gov.vn/ad/api/lienthongDVCLT/getLog?nationCode=${maHsoTrimmed}`;

      try {
        const response = await axios.get(getHsoIdUrl, {
          headers: {
            Authorization: `Bearer ${this.igateToken}`,
          },
        });
        if (response.data && response.data.content.length > 0) {
          response.data.content.forEach((element) => {
            if (element.requestBody.trangThai) {
              // Check if element already exists based on trangThai and responseBody.status
              const exists = this.data.some(
                (item) =>
                  item.requestBody.trangThai ===
                    element.requestBody.trangThai &&
                  item.responseBody.status === element.responseBody.status
              );

              if (!exists) {
                this.data.push(element);
                if (this.module == "") {
                  if (
                    element.requestBody.maTTHC == "2.000986" ||
                    element.requestBody.maTTHC == "2.001023"
                  ) {
                    this.module = "LTKS";
                  } else if (element.requestBody.maTTHC == "1.011733") {
                    this.module = "LTKT";
                  }
                }
                this.maHso2 = element.requestBody.maHoSo;
              }
            }
          });
        }

        this.data.sort(
          (a, b) => a.requestBody.trangThai - b.requestBody.trangThai
        );

        console.log("content:", response);
      } catch (error) {
        alert(error);
        if (error.response) {
          console.error("Dá»¯ liá»u pháº£n há»i lá»i:", error.response.data);
          console.error("Tráº¡ng thÃ¡i pháº£n há»i lá»i:", error.response.status);
        } else if (error.request) {
          console.error("YÃªu cáº§u lá»i:", error.request);
        } else {
          console.error("ThÃ´ng bÃ¡o lá»i:", error.message);
        }
      }
    },

    async callAgainFunction() {
      console.log(this.callAgainTextarea);
      const dayLaiUrl = `https://apiigate.gialai.gov.vn/ad/api/lienthongDVCLT/capNhatTrangThaiHoSoDVCLTHoTich`;
      try {
        const response = await axios.post(
          dayLaiUrl,
          JSON.parse(this.callAgainTextarea, null, 2),
          {
            headers: {
              Authorization: `Bearer ${this.igateToken}`, // ÄÃ­nh kÃ¨m token vÃ o header
            },
          }
        );
        if (response) {
          console.log(response.data);
          if (response.data.status == 200) {
            alert(response.data.title);
          } else {
            alert(response.data.errors.soHoSoLT[0]);
          }
        }
      } catch (error) {
        alert(error);
        if (error.response) {
          console.error("Dá»¯ liá»u pháº£n há»i lá»i:", error.response.data);
          console.error("Tráº¡ng thÃ¡i pháº£n há»i lá»i:", error.response.status);
        } else if (error.request) {
          console.error("YÃªu cáº§u lá»i:", error.request);
        } else {
          console.error("ThÃ´ng bÃ¡o lá»i:", error.message);
        }
      }
    },

    async handleButtonClick(item) {
      console.log(item);
      const dayLaiUrl = `https://apiigate.gialai.gov.vn/ad/api/lienthongDVCLT/capNhatTrangThaiHoSoDVCLTHoTich`;
      try {
        const response = await axios.post(dayLaiUrl, item.requestBody, {
          headers: {
            Authorization: `Bearer ${this.igateToken}`, // ÄÃ­nh kÃ¨m token vÃ o header
          },
        });
        if (response) {
          console.log(response.data);
          if (response.data.status == 200) {
            alert(response.data.title);
          } else {
            alert(response.data.errors.soHoSoLT[0]);
          }
        }
      } catch (error) {
        alert(error);
        if (error.response) {
          console.error("Dá»¯ liá»u pháº£n há»i lá»i:", error.response.data);
          console.error("Tráº¡ng thÃ¡i pháº£n há»i lá»i:", error.response.status);
        } else if (error.request) {
          console.error("YÃªu cáº§u lá»i:", error.request);
        } else {
          console.error("ThÃ´ng bÃ¡o lá»i:", error.message);
        }
      }
    },

    async getKetQua() {
      if (this.maHso2 == "") {
        alert("chÆ°a nháº­p mÃ£ há» sÆ¡");
        return;
      }
      const maHsoTrimmed = this.maHso2.trim().replace(/\s+/g, "");
      const getKetQuaUrl = `https://apiigate.gialai.gov.vn/pa/judicial-civil-status/--sync-dossiers?code=${maHsoTrimmed}`;
      const response = await axios.post(
        getKetQuaUrl,
        {},
        {
          headers: {
            Authorization: `Bearer ${this.igateToken}`, // ÄÃ­nh kÃ¨m token vÃ o header
          },
        }
      );
      if (response) {
        alert(response);
      }
    },
  },
};
</script>

