<template>
  <v-container>
    <v-textarea v-model="input" label="Nháº­p cÃ¡c trÆ°á»ng" rows="5"></v-textarea>

    <!-- NÃºt thá»© 1: Táº¡o @JsonProperty -->
    <v-btn
      @click="generateJsonProperty"
      style="
        color: white;
        font-weight: bold;
        width: 200px;
        padding: 10px;
        margin: 10px;
        border: 1px solid #bbbbbb !important;
        border-radius: 4px;
        box-sizing: border-box;
        font-size: 13px;
        background-color: rgb(38, 113, 184) !important;
      "
      >Táº¡o @JsonProperty</v-btn
    >

    <!-- NÃºt thá»© 2: Táº¡o JSON object -->
    <v-btn
      @click="generateJsonObject"
      style="
        color: white;
        font-weight: bold;
        width: 200px;
        padding: 10px;
        margin: 10px;
        border: 1px solid #bbbbbb !important;
        border-radius: 4px;
        box-sizing: border-box;
        font-size: 13px;
        background-color: rgb(38, 113, 184) !important;
      "
      >Táº¡o JSON Object</v-btn
    >

    <!-- NÃºt thá»© 3: Táº¡o body. format -->
    <v-btn
      @click="generateBodyFormat"
      style="
        color: white;
        font-weight: bold;
        width: 200px;
        padding: 10px;
        margin: 10px;
        border: 1px solid #bbbbbb !important;
        border-radius: 4px;
        box-sizing: border-box;
        font-size: 13px;
        background-color: rgb(38, 113, 184) !important;
      "
      >Táº¡o body. format</v-btn
    >

    <!-- Káº¿t quáº£ xuáº¥t ra -->
    <v-textarea
      v-model="output"
      label="Káº¿t quáº£"
      readonly
      rows="10"
    ></v-textarea>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      input: "", // Chuá»i Äáº§u vÃ o tá»« textarea
      output: "", // Káº¿t quáº£ sinh ra
    };
  },
  methods: {
    // HÃ m loáº¡i bá» "ps", "PS", "p0", "P0"
    cleanField(field) {
      return field.replace(/^(ps|PS|p0|P0)/, ""); // Loáº¡i bá» tiá»n tá» "ps", "PS", "p0", "P0"
    },

    // HÃ m sinh ra @JsonProperty
    generateJsonProperty() {
      const fields = this.input
        .split("\n")
        .filter((field) => field.trim() !== "");
      const cleanedFields = fields.map(this.cleanField); // Loáº¡i bá» tiá»n tá» trÆ°á»c khi xá»­ lÃ½
      const result = cleanedFields
        .map(
          (field) =>
            `@JsonProperty("${field.trim()}")\nprivate String ${field.trim()};`
        )
        .join("\n\n");
      this.output = result;
    },

    // HÃ m sinh ra JSON object
    generateJsonObject() {
      const fields = this.input
        .split("\n")
        .filter((field) => field.trim() !== "");
      const cleanedFields = fields.map(this.cleanField); // Loáº¡i bá» tiá»n tá» trÆ°á»c khi xá»­ lÃ½
      const result = cleanedFields
        .map((field) => `"${field.trim()}": ""`)
        .join(",\n");
      this.output = `{\n${result}\n}`;
    },

    // HÃ m sinh ra body.MA_HD, body.MA_KH, body.MA_CQ vÃ  dÃ²ng chá»©a dáº¥u '?'
    generateBodyFormat() {
      const fields = this.input
        .split("\n")
        .filter((field) => field.trim() !== "");
      const cleanedFields = fields.map(this.cleanField); // Loáº¡i bá» tiá»n tá» trÆ°á»c khi xá»­ lÃ½
      const resultBody = cleanedFields
        .map((field) => `body.${field.trim()}`)
        .join(",\n");
      const resultQuestionMarks = cleanedFields.map(() => "?").join(", ");
      this.output = `${resultBody},\n${resultQuestionMarks}`;
    },
  },
};
</script>
